import { configureStore, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { setMessage } from "./message";

export interface User {
    id: string,
    username: string,
    password?: string,
    email: string,
    accessToken: string,
    roles: Array<string>
}

const user = JSON.parse(localStorage.getItem("user") || '{}');

const initialState = user.username
    ? { isLoggedIn:true, user }
    : { isLoggedIn: false, user:null }

const authSlice = createSlice({
    name:'auth',
    initialState,
    reducers: {
        register: (state, action) => {
            let dataFromBackEnd: User = {
                id: "1",
                username: action.payload.username,
                email: action.payload.email,
                accessToken: "12345",
                roles: ["ADMIN", "MODERATOR"]
            }

            state.user = dataFromBackEnd;
            state.isLoggedIn = true;
            localStorage.setItem("user", JSON.stringify(dataFromBackEnd));
        },
        login: (state, action) => {
            let dataFromBackEnd: User = {
                id: "1",
                username: action.payload.username,
                email: "test@account.fr",
                accessToken: "12345",
                roles: ["ADMIN", "MODERATOR"]
            }

            state.user = dataFromBackEnd;
            state.isLoggedIn = true;
            localStorage.setItem("user", JSON.stringify(dataFromBackEnd));
        },
        logout: (state) => {
            state.isLoggedIn = false;
            state.user = null;
            localStorage.removeItem("user");
        }
    }
});

const { reducer, actions } = authSlice;
export const { register, login, logout } = actions;
export default reducer;